/* DEVICES *//* COLORS *//*Colorsecondary*//* fcolorSecondarydaylighter *//* footerBg, navColor,h_ClaimBackgroundColor  *//*footerNavColor, fcolorPrimary *//*fcolorLight*//*TIPOGRAFIAS *//* MEDIDAS *//* MEDIDAS ASIDE */* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

.header {
  position: fixed;
  top: 0px;
  z-index: 3;
  font-family: "RubiK";
}

.header .header-puzzle {
  background-color: #099D8D;
  opacity: 0.95;
  padding: 0.93rem;
  height: 3.75rem;
  width: 100%;
}

.header .title {
  color: white;
  font-weight: 800;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-size: 1rem;
  padding-top: 0.62rem;
  text-decoration: none;
}

.menu {
  background-color: #14D9C4;
  opacity: 0.9;
  padding-top: 0.93rem;
  font-size: 0.93rem;
  font-weight: 800;
  height: 2.81rem;
  text-align: center;
  position: relative;
  top: 0;
  width: 100vw;
}

.menu ul li {
  display: inline;
  padding-right: 0.62rem;
}

.menu ul li a {
  text-decoration: none;
  color: white;
  padding-top: 0.5rem;
  padding-bottom: 0.43rem;
  left: 1rem;
  right: 1.06rem;
}

.menu ul li a:hover {
  text-decoration: underline;
  font-size: 1rem;
}

.hero {
  background: url(../images/computer.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 65vh;
}

.csection {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0pc 30px 0px;
  background-color: white;
}

.csection__title {
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.88;
  letter-spacing: normal;
  margin: 0px auto;
  text-transform: uppercase;
  color: #14D9C4;
  font-family: "RubiK";
}

.csection__text {
  width: 80%;
  margin: 0px auto;
  padding-top: 15px;
}

.csection__text p {
  font-size: 1rem;
  line-height: 1.5;
  letter-spacing: normal;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  text-align: center;
  color: #54585A;
  font-family: "Open Sans", sans-serif;
}

.csection__text h5 {
  padding-top: 25px;
  text-align: center;
  color: #54585A;
  font-family: "Open Sans", sans-serif;
}

.csection__text span {
  color: #14D9C4;
}

span {
  color: #14D9C4;
}

label {
  padding-top: 25px;
  color: #54585A;
  font-family: "Open Sans", sans-serif;
}

.form__content {
  width: 80vw;
  margin: 0px auto;
  border-radius: 5px;
  padding: 20px;
  padding-bottom: 70px;
  box-sizing: border-box;
}

.form__content--email input {
  font-size: 16px;
  color: #54585A;
}

.form__content--email input ::placeholder {
  font-size: 16px;
  color: #54585A;
}

.form__content--submit {
  padding-top: 24px;
}

.form__content--submit input {
  border-radius: 4px;
  border: none;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  text-transform: uppercase;
  background-color: #099D8D;
  color: white;
}

.form__content--submit input :hover {
  background-color: #14D9C4;
}

input,textarea {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: block;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  color: #B8B8B9;
  border: 1px solid #B8B8B9;
}

textarea {
  resize: none;
}

hr {
  width: 99.5%;
  height: 0.5px;
  background-color: #b8b8b9;
  opacity: 70%;
}

.f-wrapper {
  display: grid;
  width: 100%;
  height: 250px;
  grid-template-columns: 74px 1fr 74px;
  grid-template-rows: 30px 20px 20px 60px 30px 45px 45px;
}

.item--1 {
  grid-column: 2/3;
  grid-row: 2/3;
  font-family: "Open Sans", sans-serif;
  color: #b8b8b9;
  text-align: center;
  font-size: 85%;
}

.item--2 {
  grid-column: 2/3;
  grid-row: 6/7;
  background-image: url(../images/adalablogo.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  display: inline-block;
  background-position: center;
}

.item--3 {
  grid-column: 2/3;
  grid-row: 4/5;
  font-family: "Open Sans", sans-serif;
  font-size: 82%;
  line-height: 20px;
  opacity: 70%;
  text-align: center;
  padding: 0;
  line-height: 25px;
}

.item--3 ul {
  padding: 0%;
}

.item--3 ul li a {
  color: #54585A;
  text-decoration: none;
}

@media all and (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-content: center;
    background-color: #099D8D;
    opacity: 0.9;
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0;
  }

  .header .title {
    text-align: left;
    margin-left: 2.81rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }

  .menu {
    width: 100%;
    padding-top: 22.5px;
    background-color: #099D8D;
    opacity: 0.9;
    text-align: right;
    padding-right: 45px;
  }

  .hero {
    justify-content: flex-start;
    padding-left: 5%;
    height: 40vh;
    background-position: center;
  }

  .f-wrapper {
    height: 161px;
    grid-template-columns: 45px 172px 1fr 324px 90px;
    grid-template-rows: 30px 20px 15px 45px 51px;
    border: solid 1px white;
  }

  .item--1 {
    display: inline-block;
    opacity: 75%;
    font-family: "Open Sans", sans-serif;
    color: #b8b8b9;
    text-align: left;
    font-size: 85%;
    grid-column: 2/3;
    grid-row: 2/3;
  }

  .item--2 {
    display: inline-block;
    background-position: left;
    padding-top: 15px;
    grid-row: 4/5;
    grid-column: 2/3;
  }

  .item--3 {
    display: inline-block;
    padding-top: 15px;
    grid-column: 4/5;
    grid-row: 2/5;
    text-align: right;
    padding: 0;
  }
}

@media all and (min-width: 1200px) {
  .header .title {
    margin-left: 138px;
  }

  .menu {
    text-align: right;
    padding-right: 138px;
  }

  .hero {
    justify-content: flex-start;
    height: 60vh;
    background: url(../images/computer.jpg);
    background-position: center;
    background-size: cover;
  }

  .form__content--mailphone {
    display: flex;
    justify-content: space-between;
  }

  .form__content--email input {
    width: 38vw;
  }

  .form__content--phone input {
    width: 38vw;
  }

  .form__content--submit input {
    width: 38vw;
  }

  .form__content--submit {
    display: flex;
    justify-content: flex-end;
    text-align: end;
  }

  .f-wrapper {
    height: 161px;
    grid-template-columns: 138px 172px 1fr 324px 138px;
    grid-template-rows: 30px 20px 15px 45px 51px;
  }
}
